import {
    LOADING_UI,
    LOADING_DATA,
    UPLOAD_POST,
    SET_MANY_POSTS,
    SET_POST,
    SET_DATA_ERROR,
    SET_RESPONSE_MESSAGE,
} from '../types'
import handleLikeByUser from '../../util/handleLike'
import axios from 'axios'

export const getPosts = () => (dispatch) => {
    dispatch({ type: LOADING_DATA })
    axios.get('/api/posts', {
        params: {
            lastDate: new Date(),
            limit: 10
        }
    }). then(res => {
            let userId = localStorage.userId
            let posts = handleLikeByUser(res.data, userId)
            dispatch({
                type: SET_MANY_POSTS,
                payload: posts
            })
        })
        .catch(err => {
            // console.log(err)
            dispatch({
                type: SET_DATA_ERROR,
                payload: err.response.data
            })
        })
}

export const getPost = (postId) => (dispatch) => {
    dispatch({
        type: SET_POST
    })
}

export const uploadPost = (post) => (dispatch) => {
    dispatch({ type: LOADING_UI})
    axios.post('/api/newpost', post)
         .then(res => {
             dispatch({
                 type: UPLOAD_POST,
                 payload: res.data.post
             })
             dispatch({
                 type: SET_RESPONSE_MESSAGE,
                 payload: res.data
             })
         })
         .catch(err => {
             dispatch({
                 type: SET_DATA_ERROR,
                 payload: err.response.data
             })
         }) 
}