import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Header from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

import { connect } from 'react-redux'
import { toggleSideNav } from '../../redux/actions/uiActions'

const styles = (theme) => ({
  root: {
    flexGrow: 1,
    "margin-bottom": "25px"
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
});

class AppBar extends React.Component {
  render(){
    const { classes, toggleSideNav} = this.props;
    return (
        <div className={classes.root}>
          <Header position="static">
            <Toolbar>
              <IconButton edge="start" className={classes.menuButton} color="inherit" onClick = {toggleSideNav}>
                <MenuIcon />
              </IconButton>
              <Typography variant="h6" className={classes.title}>
                News
              </Typography>
              <Button color="inherit">My account</Button>
            </Toolbar>
          </Header>
        </div>
      );
  }
}

const mapActionsToProps = {
    toggleSideNav
}

export default connect(
    null,
    mapActionsToProps
)(withStyles(styles)(AppBar))
