import React from 'react';
import { withStyles } from '@material-ui/core/styles'
import { Button, Paper, TextField, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@material-ui/core/';
import {
  Typography,
  ButtonBase,
  Avatar
} from '@material-ui/core';

import PostOptions from './postOption'
import CircularProgress from '@material-ui/core/CircularProgress';
import SnackBar from './snackBar'

import { connect } from 'react-redux'
import { uploadPost } from '../../redux/actions/dataActions'

const styles = {
    container: {
      width: "600px",
      "position": "relative",
      "left": "50%",
      "top": "50%",
      "transform": "translate(-50%, 0%)",
      "background-color": "white",
      "box-shadow": "0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12)",
      transition: "0.3s",
      "padding": "20px 0px 20px 20px",
      "justify-content": "flex-start",
      '&:hover': {
        "background-color": "#d9d9d9"
      }
    },
    postField: {
        "position": "relative",
        "left": "50%",
        "top": "50%",
        "transform": "translate(-50%, 0%)",
        width: "300px"
    },
    placeHolder: {
      opacity: 0.4
    },
    avatar: {
      "background-color": "rgb(0, 188, 212)",
      "margin-right": "10px"
    },
    dialog: {
      width: "400px",
      height: "300px"
    },
    fieldContainer: {
      "overflow-y": "hidden"
    },
    postHeader: {
      display: "flex",
      "justify-content": "space-between"
    }
}

class PostWriter extends React.Component{
    constructor(props) {
        super(props)
        this.state = {
            open: false,
            postContent: "",
            snackBarOpen: false
        }
    }
    componentWillReceiveProps(nextProps) {
      if(nextProps.UI.loading === false && this.props.UI.loading === true) {
        this.closeDialog()
        this.setState({snackBarOpen: true})
      }
    }
    openDialog = () => {
        this.setState({open: true})
    }
    closeDialog = () => {
        this.setState({open: false})
    }
    closeSnackBar = () => {
        this.setState({snackBarOpen: false})
    }
    handleChange = (e) => {
      this.setState({postContent: e.target.value})
    }
    handleUpload = () => {
      const newPost = {
        content: this.state.postContent
      }
      this.props.uploadPost(newPost)
    }
    render() {
        const { classes, UI: { loading, errors, message: { message } } } = this.props;
        return(
            <div>
            <ButtonBase className = {classes.container} onClick = {this.openDialog}>
              <Avatar className = {classes.avatar}>R</Avatar>
              <Typography className = {classes.placeHolder}>What are you thinking</Typography>
            </ButtonBase>
            <Dialog open={this.state.open} onClose={this.closeDialog}>
              <div className = {classes.dialog}>
                <DialogTitle id="form-dialog-title">
                  <div className = {classes.postHeader}>
                    <Avatar className = {classes.avatar}>R</Avatar>
                    <PostOptions/>
                  </div>
                </DialogTitle>
                <DialogContent className = {classes.fieldContainer}>
                  <TextField
                      autoFocus
                      className = {classes.postField}
                      margin="dense"
                      placeholder = "What are you thinking"
                      multiline
                      rows = {2}
                      rowsMax = {4}
                      onChange = {this.handleChange}
                  />
                </DialogContent>
                <DialogActions>
                  <Button onClick={this.closeDialog} color="primary" disabled = {loading}>
                    Cancel
                  </Button>
                  <Button onClick={this.handleUpload} color="primary" disabled = {loading}>
                    {loading ? (
                          <CircularProgress size={24} />
                      ) : (
                          "Post"
                      )}
                  </Button>
                </DialogActions>
              </div>
            </Dialog>
            <SnackBar
                  errors = {errors} 
                  message = {message} 
                  open = {this.state.snackBarOpen}
                  handleClose = {this.closeSnackBar}
             />
          </div>
        )
    }
}

const mapStateToProps = (state) => ({
  UI: state.UI
})

const mapActionsToProps = {
  uploadPost
}

export default connect(
  mapStateToProps,
  mapActionsToProps,
)(withStyles(styles)(PostWriter))
