import React from 'react';

function handleLikeByUser(posts, userId) {
	let modifiedPosts = posts;
    for(let i = 0; i < modifiedPosts.length; i++) {
		if(modifiedPosts[i].likes.indexOf(userId) !== -1) {
			modifiedPosts[i].likedByUser = true
		}
		else {
			modifiedPosts[i].likedByUser = false
		}
	}
    return modifiedPosts
}

export default handleLikeByUser;