import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import dayjs from 'dayjs'
import relativeTime from 'dayjs/plugin/relativeTime';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import FavoriteIcon from '@material-ui/icons/Favorite';
import Comment from '@material-ui/icons/Comment'
import Share from '@material-ui/icons/Share'
import Typography from '@material-ui/core/Typography';
import { likePost, unlikePost } from '../../redux/actions/likeActions'

import { connect } from 'react-redux'

const styles = {
  card: {
    width: "600px",
    "position": "relative",
    "left": "50%",
    "top": "50%",
    "transform": "translate(-50%, 0%)",
    "margin-top": "25px"
  },
  avatar: {
      "background-color": "rgb(0, 188, 212)",
  },
  action: {
      display: "flex",
      "justify-content": "space-around",
  },
  liked: {
      "color": "rgb(0, 188, 212)"
  },
  number: {
    position: "relative",
	right: "6px"
  }
};

class Post extends React.Component{
    render() {
        const { classes, post, likeStatus, likePost, unlikePost } = this.props;
        dayjs.extend(relativeTime);
        return(
            <Card key = {post._id} className={classes.card}>
                <CardHeader
                    avatar={
                    <Avatar className={classes.avatar}>
                        L
                    </Avatar>
                    }
                    title = {post.author}
                    subheader= {dayjs(post.createdAt).fromNow()}
                />
                <CardContent>
                <Typography variant="body2" component="p">
                    {post.content}
                </Typography>
                </CardContent>
                <CardActions className = {classes.action}>
                    <div>
                        <IconButton
                            title = "Like" 
                            onClick = {(likeStatus) ? () => unlikePost(post._id)
                                                        : () => likePost(post._id)}
                        >
                            <FavoriteIcon className = {(likeStatus) ? classes.liked : ""}/>
                        </IconButton>
                        <Typography variant = "caption" className = {classes.number}>
                            {post.likeCount}
                        </Typography>
                    </div>
                    <div>
                        <IconButton title = "Comment">
                            <Comment/>
                        </IconButton>
                        <Typography variant = "caption" className = {classes.number}>
                            {post.commentCount}
                        </Typography>
                    </div>
                    <IconButton title = "Share">
                        <Share/>
                    </IconButton>
                </CardActions>
            </Card>
        )
    }
}

const mapActionsToProps = {
    likePost,
    unlikePost
}

export default connect(
    null,
    mapActionsToProps
)(withStyles(styles)(Post))